.page {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  position: relative;
}
.page__main {
  flex: 1 1 auto;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.clear-list {
  list-style-type: none;
  margin: 0;
  padding: 0;
}
@font-face {
  font-family: "Lato";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/lato/Lato-Regular.woff2") format("woff2");
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/oswald/oswaldregular.woff2") format("woff2");
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: url("../fonts/oswald/oswaldmedium.woff2") format("woff2");
}
html {
  width: 100%;
}
body {
  height: 100%;
  font-family: "Lato", "Times New Roman", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  color: #000000;
  margin: 0;
}
.primary-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 40px;
  box-sizing: border-box;
  padding: 5px 10px;
  font-family: "Oswald", "Times New Roman", sans-serif;
  font-size: 16px;
  line-height: 20px;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  color: #ffffff;
  background-color: #68b738;
  overflow: hidden;
  text-overflow: ellipsis;
  border: none;
  outline: none;
  cursor: pointer;
}
.primary-btn:hover {
  color: rgba(255, 255, 255, 0.6);
  background-color: rgba(94, 170, 47, 0.6);
}
.primary-btn:focus {
  color: rgba(255, 255, 255, 0.6);
  background-color: rgba(94, 170, 47, 0.6);
  outline: none;
}
.primary-btn:active {
  color: rgba(255, 255, 255, 0.3);
  background-color: rgba(94, 170, 47, 0.3);
}
.secondary-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 4px 0;
  font-family: "Oswald", "Times New Roman", sans-serif;
  font-size: 16px;
  line-height: 16px;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  color: #000000;
  border: none;
  outline: none;
  cursor: pointer;
}
.secondary-btn:hover {
  color: rgba(0, 0, 0, 0.6);
}
.secondary-btn:focus {
  color: rgba(0, 0, 0, 0.6);
  outline: none;
}
.secondary-btn:active {
  color: rgba(0, 0, 0, 0.3);
}
@media (min-width: 768px) {
  .secondary-btn {
    font-size: 20px;
    line-height: 20px;
  }
}
.third-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  box-sizing: border-box;
  min-height: 40px;
  padding: 10px;
  font-family: "Oswald", "Times New Roman", sans-serif;
  font-size: 16px;
  line-height: 20px;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  color: #444444;
  background-color: #f2f2f2;
  overflow: hidden;
  text-overflow: ellipsis;
  border: none;
  outline: none;
  cursor: pointer;
}
.third-btn:hover {
  color: rgba(68, 68, 68, 0.6);
  background-color: rgba(235, 235, 235, 0.6);
}
.third-btn:focus {
  color: rgba(68, 68, 68, 0.6);
  background-color: rgba(235, 235, 235, 0.6);
  outline: none;
}
.third-btn:active {
  color: rgba(68, 68, 68, 0.3);
  background-color: rgba(235, 235, 235, 0.3);
}
.input-field {
  font-family: "Oswald", "Times New Roman", sans-serif;
  font-size: 16px;
  line-height: 18px;
  text-align: left;
  text-decoration: none;
  text-transform: uppercase;
  color: #444444;
  background-color: transparent;
  border: 1px solid #e7e7e7;
  outline: none;
  padding: 10px;
}
.input-field:hover:not([type="radio"]):not([type="checkbox"]) {
  border: 1px solid #cdcdcd;
  outline: 1px solid #cdcdcd;
}
.input-field:focus:not([type="radio"]):not([type="checkbox"]) {
  border: 1px solid #444444;
  outline: 1px solid #444444;
}
.input-field:focus + .configuration-group__icon {
  stroke: #68b738;
}
.input-field:active:not([type="radio"]):not([type="checkbox"]) {
  border: 1px solid #444444;
  outline: 1px solid #444444;
}
.input-field:active + .configuration-group__icon {
  stroke: #68b738;
}
.input-field:focus:invalid {
  border: 1px solid #ff8282;
  outline: 1px solid #ff8282;
}
.input-field:focus:invalid + .configuration-group__icon {
  stroke: #ff8282;
}
.input-field::placeholder {
  font-family: "Oswald", "Times New Roman", sans-serif;
  font-size: 16px;
  line-height: 18px;
  text-align: left;
  text-decoration: none;
  text-transform: uppercase;
  color: #444444;
}
@media (min-width: 768px) {
  .input-field {
    font-size: 20px;
    line-height: 20px;
  }
}
.header {
  padding-top: 25px;
  padding-bottom: 10px;
  z-index: 50;
  position: relative;
  overflow: hidden;
}
.header__container {
  max-width: 320px;
  margin: 0 auto;
  padding: 0 20px;
  box-sizing: border-box;
}
@media (min-width: 768px) {
  .header__container {
    max-width: 708px;
  }
}
@media (min-width: 1440px) {
  .header__container {
    max-width: 1220px;
  }
}
.header__wrapper {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  align-self: center;
  justify-content: space-between;
  min-height: 40px;
  padding-top: 10px;
  position: relative;
}
.header__burger--opened,
.header__burger--closed {
  background-position: center;
  background-repeat: no-repeat;
  background-color: transparent;
  border: none;
  outline: none;
  cursor: pointer;
}
.header__burger--opened:hover,
.header__burger--closed:hover {
  opacity: 0.8;
}
.header__burger--opened:focus,
.header__burger--closed:focus {
  opacity: 0.8;
}
.header__burger--opened:active,
.header__burger--closed:active {
  opacity: 0.6;
}
.header__burger--opened {
  background-image: url("../img/logo/burger_open.svg");
  width: 24px;
  height: 24px;
}
.header__burger--closed {
  background-image: url("../img/logo/burger_close.svg");
  width: 18px;
  height: 18px;
}
.header__navigation--opened {
  display: block;
}
.header__navigation--closed {
  display: none;
}
@media (min-width: 768px) {
  .header {
    padding-top: 20px;
    padding-bottom: 20px;
  }
  .header__wrapper {
    min-height: 50px;
    justify-content: left;
    padding-top: 15px;
  }
  .header__burger {
    display: none;
  }
  .header__navigation--opened {
    display: block;
  }
  .header__navigation--closed {
    display: block;
  }
}
@media (min-width: 1440px) {
  .header {
    padding-top: 45px;
  }
  .header__wrapper {
    min-height: 60px;
  }
}
.navigation {
  position: relative;
  width: 100%;
  padding-top: 10px;
  background-color: #ffffff;
}
.navigation__wrapper {
  position: relative;
}
.navigation__item {
  position: relative;
}
.navigation__item::before {
  content: "";
  position: absolute;
  top: 0;
  left: -350%;
  bottom: 0;
  width: 600%;
  height: 1px;
  background-color: #e6e6e6;
}
.navigation__link {
  display: block;
  font-family: "Oswald", "Times New Roman", sans-serif;
  font-size: 20px;
  font-weight: 400;
  line-height: 20px;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  color: #000000;
  padding: 20px;
  outline: none;
  border: none;
  cursor: pointer;
}
.navigation__link:hover {
  color: rgba(0, 0, 0, 0.6);
}
.navigation__link:focus {
  color: rgba(0, 0, 0, 0.6);
}
.navigation__link:active {
  color: rgba(0, 0, 0, 0.3);
}
@media (min-width: 768px) {
  .navigation {
    width: auto;
    margin-left: auto;
    padding-top: 0;
  }
  .navigation__list {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    justify-self: center;
    align-items: center;
  }
  .navigation__item {
    padding: 10px;
  }
  .navigation__item::before {
    display: none;
  }
  .navigation__link {
    font-size: 18px;
    line-height: 24px;
    padding: 0;
  }
  .navigation__link--current {
    position: relative;
  }
  .navigation__link--current::after {
    content: "";
    position: absolute;
    bottom: -12px;
    left: 0;
    width: 100%;
    height: 2px;
    background-color: #68b738;
    z-index: 2;
  }
}
@media (min-width: 1440px) {
  .navigation {
    padding: 0;
    background-color: transparent;
  }
  .navigation__list {
    margin-left: auto;
  }
  .navigation__link {
    font-size: 20px;
    line-height: 20px;
    color: #ffffff;
  }
  .navigation__link--catalog,
  .navigation__link--form {
    color: #000000;
  }
  .navigation__link--current-main::after {
    background-color: #ffffff;
  }
  .navigation__link--current-main:hover::after {
    background-color: rgba(0, 0, 0, 0.6);
  }
  .navigation__link--current-main:focus::after {
    background-color: rgba(0, 0, 0, 0.6);
  }
  .navigation__link--current-main:active::after {
    background-color: rgba(0, 0, 0, 0.3);
  }
}
.logo {
  align-items: center;
  margin: 0;
  padding: 0;
  border: none;
  outline: none;
}
.logo--txt {
  width: 101px;
  height: 18px;
}
.logo__icon--img:hover {
  opacity: 0.8;
}
.logo__icon--txt:hover {
  opacity: 0.8;
}
.logo__icon--img:focus {
  opacity: 0.8;
}
.logo__icon--txt:focus {
  opacity: 0.8;
}
.logo__icon--img:active {
  opacity: 0.6;
}
.logo__icon--txt:active {
  opacity: 0.6;
}
@media (min-width: 768px) {
  .logo--img {
    margin-right: 0;
  }
  .logo--txt {
    justify-content: flex-start;
    margin-left: 16px;
  }
}
@media (min-width: 1440px) {
  .logo--txt {
    width: 118px;
    height: 21px;
  }
}
